            speechResultSuccess(result){
                console.log("success "+result);

            },
            speechResultError(message){
                 // _this.sendVal = false;
                console.log("err" + message);
            },
            stopSpeech(){
                var _this = this;
                var ifly = cordova.plugins.IflyForXwjPlugin;
                this.isPressSpeechBtn = false;
                this.$refs.voiceBtnKeyBoard.style.background = "#FFFFFF";
                if(_this.sendVal){
                    _this.sendVal = false;
                    ifly.stopListen(function(spRes){
                    console.log("success "+spRes);
                        var showResultTimer;
                        if (spRes) {
                            correctKeywordService({'keyword':spRes}).then(res =>{
                                if (res.resultCode==="0"){
                                    _this.recognizeResult = res.data;
                                    _this.goToGoodsSearch(res.data)
                                }else {
                                    _this.isNoRecognizeResult = true;
                                    _this.isShowVoiceBtn = true;
                                }
                            }).catch(error =>{
                                _this.isNoRecognizeResult = true;
                                _this.isShowVoiceBtn = true;
                            });
                            _this.isShowSpeechResult = true;
                            //收起键盘
                            _this.$refs.inputSearch.blur();
                            _this.focusState = false;
                            _this.isShowVoiceBtn = false;
                            // showResultTimer = setTimeout(() => {
                            //     _this.goToGoodsSearch(resultcatch);
                            // }, 1000)
                        } else {
                            _this.isNoRecognizeResult = true;
                             _this.isShowVoiceBtn = true;
                            console.log('没有值')
                        }
                },function(errMes){
                    _this.isNoRecognizeResult = true;
                    _this.isShowVoiceBtn = true;
                    console.log("err" + errMes+" || "+_this.isShowVoiceBtn);
                },null);
                }else{
                    _this.isShowVoiceBtn = false;
                    if (_this.isHasAudioPermissions){
                        Toast('说话时间太短，请长按说话');
                        _this.isShowSpeechDiv =false;
                    }
                    _this.isShowSpeechResult=false;
                    _this.sendVal = false;
                }
                console.log("停止录音 "+this.pressTimer);
                clearTimeout(this.pressTimer);
                this.pressTimer = null;
            },
            //语音搜索
            goToSpeech(e){
                var _this = this;
                this.pressTimer = null;
                _this.isNoRecognizeResult = false;
                this.isShowSpeechResult=false;
                var ifly = cordova.plugins.IflyForXwjPlugin;
                _this.recognizeResult = "正在聆听";
                if(e.type === 'click' && e.button !==0){
                    return;
                }
                this.isPressSpeechBtn = true;
                this.$refs.voiceBtnKeyBoard.style.background = "#D34050";
                this.isNoRecognizeResult = false
                if (_this.isHasAudioPermissions){
                    if(this.pressTimer ===null) {
                        this.pressTimer = setTimeout(()=>{
                            _this.sendVal=true;
                            _this.isShowSpeechDiv = true;
                            ifly.startListen(
                                null,null,false,true
                            );
                        },300)
                    }
                }else {
                    Dialog.confirm({
                        message: "请您打开麦克风权限，以正常使用语音识别功能"
                    }).then(() => {
                        _this.checkAudioPermissions();
                    }).catch(() => {
                        //oncancel
                    })
                }

            },
            checkAudioPermissions() {
                let _this = this;
                if (this.$devicePlatform == 'ios') {
                    // this.isHasAudioPermissions = true;
                    // return true;
                    var permissionPlugin = cordova.plugins.iOSPermission;
                        permissionPlugin.checkMicrophonePermission(
                        res => {
                            this.isHasAudioPermissions = res== '0' ? false : true;
                        },
                        err => {
                            this.isHasAudioPermissions = false;
                        }
                    );
                } 
                else if (this.$devicePlatform == 'android') {
                    var list = ["android.permission.RECORD_AUDIO"];
                    var permissions = cordova.plugins.permissions;
                    permissions.requestPermissions(
                        list,
                        function(status) {
                            if (status.hasPermission) {
                            _this.isHasAudioPermissions = true;
                            }
                        },
                        function(status) {
                            alert("请打开麦克风权限");
                            _this.isHasAudioPermissions = false;
                        }
                    );
                }
                else {
                    this.isHasAudioPermissions = false;
                }
            
            },
            bodyScroll(event){
                event.preventDefault();
            },
            // //禁止滑动
            // forbidBodyScroll(){
            //     document.querySelector(".post-wrapper").addEventListener('touchmove', this.bodyScroll, { passive: false });
            // },
            // //恢复滑动
            // allowBodyScroll(){
            //     document.querySelector(".post-wrapper").removeEventListener('touchmove', this.bodyScroll, { passive: false });
            // },
            /**
             * 获取搜索历史记录
             * ownerCode Number 业主CODE
             */
            getSearchHis(){
                let that = this;
                let paramData = {
                    ownerCode: this.$userInfo.OWNER_CODE
                }
                getSearchHisService(paramData).then(res => {
                    if (res.resultCode==0){
                        this.SearchHisList = res.data;
                        this.historyKeyWordAll = res.data;
                        this.$nextTick(()=>{
                            that.historyWordVisible = false;
                            if(that.$refs.historyBox.clientHeight>that.$refs.historyList.clientHeight){
                                that.isShowMoreHistory = true;
                                that.isShowSpeechDiv=false;
                                that.historyKeyWordSection = that.historyKeyWordAll.slice(0, 1);
                                that.calculateHistoryShowMore(0);
                            }else{
                                that.isShowMoreHistory = false;
                                that.historyWordVisible = true;
                            }
                        })
                    }
                }).catch(error => {
                });
            },
            // 获取搜索热搜词列表
            getSearchHotWord(){
                let that = this;
                let paramData = {
                    areaCode: this.$userInfo.AREA_CODE
                }
                getSearchHotWordService(paramData).then(res => {
                    if (res.resultCode==0 && res.data){
                        that.hotSearchWord = res.data;
                    }
                }).catch(error => {
                    console.log('搜索热搜词列表获取失败： ' + error);
                });
            },

            //计算历史搜索词是否显示更多
            calculateHistoryShowMore(index) {
                if (this.isShowMoreHistory) {
                    if (this.historyKeyWordAll && index < this.historyKeyWordAll.length) {
                        this.SearchHisList = this.historyKeyWordAll.slice(0, index+1);

                        this.$nextTick(()=>{
                            if(this.$refs.historyBox.clientHeight > this.$refs.historyList.clientHeight){
                                this.SearchHisList = this.historyKeyWordSection;
                                this.historyWordVisible = true;
                            }else{
                                this.historyKeyWordSection = this.SearchHisList;
                                this.calculateHistoryShowMore(index+1);
                            }
                        });
                    }
                }
            },
            //点击历史记录标签进行搜索
            historyItemBtn(searchKey){
                this.searchKey = searchKey;
                this.goodsSearch(1);
            },
            //点击查看更多历史记录按钮
            moreHistoryBtn(){
                this.moreHistory = !this.moreHistory;
                this.SearchHisList = this.moreHistory ? this.historyKeyWordAll : this.historyKeyWordSection;

            },
            //点击删除历史记录按钮
            closeHistoryBtn(){
                Dialog.confirm({
                    message: '确定删除全部历史记录吗？'
                }).then(() => {
                    // on confirm
                    this.dropSearchHis();
                }).catch(() => {
                    // on cancel
                });
            },
            /**
             * 删除历史记录
             * ownerCode  Number 业主CODE
             */
            dropSearchHis(){
                let paramData = {
                    ownerCode: this.$userInfo.OWNER_CODE
                }
                dropSearchHisService(paramData).then(res => {
                    if (res.resultCode==0){
                        this.SearchHisList = [];
                        this.toastPopMsg('删除成功');
                    }
                    else {
                        this.toastPopMsg('删除失败，请检查网络');
                    }
                }).catch(error => {
                    this.toastPopMsg('删除失败，请检查网络');
                });
            },
            emptySearch(){
                this.searchKey = "";
            },
            //重置搜索
            showFixedSearch(){
                // this.searchKey = "";
                this.sortMethod = "";
                this.shopSortMethod = '';
                this.goodsListData = [];
